	module.exports = {"data":{"site":{"siteMetadata":{"title":"Solar Sailer","author":"Matthieu Oger"}},"markdownRemark":{"html":"<p><a href=\"http://facebook.github.io/origami/\">Origami</a> is a plugin for <a href=\"http://en.wikipedia.org/wiki/Quartz_Composer\">Quartz Composer</a>, developed by Facebook.</p>\n<p><img src=\"/images/posts/2014-02-05-origami.jpg\" alt=\"Origami\"></p>\n<p>Initially, Quartz Composer is a “visual programming” environment to create animated scenes.</p>\n<p>But recently, <a href=\"https://medium.com/the-year-of-the-looking-glass/af182add5a2f\">some designers</a> have started to use it to mockup animations and interactions.</p>\n<p>Because the primary goal of Quartz Composer is to create motion graphics, it makes sense to use it for interaction prototyping. The problem is that there isn't a lot of patches (a patch is like an instance of a class in a classic object-oriented programming language) dedicated to user interface in Quartz Composer.</p>\n<p>That's where Origami steps in:</p>\n<blockquote>\n<p>Origami provides a set of tools for Quartz Composer that make interaction prototyping a lot easier.</p>\n<p>Animations, transitions, buttons, layers, phones, etc.</p>\n</blockquote>\n<p>I recommend to watch <a href=\"https://vimeo.com/85578380\">this video</a> to get a brief overview of what is possible with Origami. It's intriguing, it looks promising and it motivates me to learn Quartz Composer more seriously.</p>","frontmatter":{"date":"2014-02-05","readableDate":"05 February 2014","title":"Interaction prototyping with Origami","showPageTitle":null},"fields":{"path":"/2014/02/origami-quartz-composer/"}}},"pathContext":{"id":"/Users/ashen/Code/solarsailer.net/content/posts/2014-02-05-origami-quartz-composer.md absPath of file >>> MarkdownRemark"}}

